import { _decorator, Component, Label, Node } from 'cc';
import { BetZoneComponent } from './BetZoneComponent';
const { ccclass, property } = _decorator;

@ccclass('LabelUi')
export class LabelUi extends Component {
  @property(Label)
  totalBetLabel: Label = null;
  @property(Node)
  tiles: Node = null;

  start() {}

  private value: number = 0;

  updateTotalBetValue() {
    this.totalBetLabel.string = `Bet: ${this.value}`;
  }

  update(deltaTime: number) {
    // this.getTotalBets();
  }

  getTotalBets() {
    this.tiles.children.forEach((childNode) => {
      const betZoneComponent =
        childNode.getComponentInChildren(BetZoneComponent);
      if (betZoneComponent) {
        this.value += betZoneComponent.getTotalValue();
        this.updateTotalBetValue();
      }
    });
  }
}
